import axios from 'axios'
const url = "/api/users"

class UserService {
  static get() {
    return new Promise( async (resolve, reject) => {
      try {
        const res = await axios.get(url)
        const data = res.data
        resolve(
          data.map(user => ({
            ...user
          }))
          )
        } catch(err) {
          reject(err);
        }
      })
    }
    static edit(_id, _field, _value) {
      return new Promise( async (resolve, reject) => {
        try {
          await axios.post(url + '/updOneField', {id: _id, field: _field, value: _value})
          resolve()
        } catch(err) {
          reject(err);
        }
      })
    }
  }
  
  export default UserService;