<template>
  <div>
    <!-- Верхнее меню -->
    <div style="margin-bottom: 16px">
      <a-button 
        type="primary" 
        @click="showModal"
      >
        Добавить
      </a-button>
    </div>

    <a-table 
      :columns="columns"
      :data-source="teams"
      :row-key="'_id'"
      size="small"
      :pagination="{pageSize: 20}"
    >
      <template
        slot="name"
        slot-scope="text, record"
      >
        <EditableCell
          :text="text"
          @change="onCellChange(record._id, 'name', $event)"
        />
      </template>
      <span
        slot="users"
        slot-scope="text, record"
      >
        <a-tag
          v-for="user in record.users"
          :key="user"
          color="blue"
        >{{ users.find(x => x._id === user).fullname }}</a-tag>
      </span>
      <span
        slot="start_date"
        slot-scope="start_date"
      >
        {{ strToDay(start_date) }}
      </span>
      <span
        slot="end_date"
        slot-scope="end_date"
      >
        {{ strToDay(end_date) }}
      </span>
    <!-- <span
      slot="action"
      slot-scope="text, record"
    >
      <a
        href="javascript:;"
        @click="handleAdd(record._id)"
      >Пригласить 一 {{ record.name }}</a>
      <a-divider type="vertical" />
      <a
        href="javascript:;"
        @click="onDelete(record.id)"
      >Удалить</a>
      <a-divider type="vertical" />
      <a
        href="javascript:;"
        class="ant-dropdown-link"
      >
        Больше действий
        <a-icon type="down" />
      </a>
    </span> -->
    </a-table>
  </div>
</template>
<script>

import store from '@/store'
import TeamService from "@/controllers/TeamService"
import EditableCell from '@/components/helpers/EditableCell'

const columns = [{
  dataIndex: 'name',
  key: 'name',
  title: "Название",
  scopedSlots: { customRender: 'name' },
},{
  title: 'Очки',
  dataIndex: 'points',
  key: 'points',
},{
  title: 'Состав',
  dataIndex: 'users[0]',
  key: 'users',
  scopedSlots: {customRender: 'users'},
},{
  title: 'Начало',
  dataIndex: 'dates[0]',
  key: 'start_date',
  scopedSlots: {customRender: 'start_date'},
},{
  title: 'Конец',
  dataIndex: 'dates[1]',
  key: 'end_date',
  scopedSlots: {customRender: 'end_date'},
}
// ,{
//   title: 'Действия',
//   key: 'action',
//   scopedSlots: { customRender: 'action' },
// }
]

export default {
  components: {
    EditableCell,
  },
  data() {
    return {
      columns: columns
    }
  },
  computed: {
    teams: function () {
      return this.$store.getters.TEAMS
    },
    users: function () {
      return this.$store.getters.USERS
    }
  },
  methods: {
    onCellChange (id, dataIndex, key) {
        const target = this.teams.find(item => item['_id'] === id)
        if (target) {
          target[dataIndex] = key
          TeamService.editTeam(id, dataIndex, key)
        }
    },
    onDelete (key) {
      const dataSource = [...this.teams]
      this.teams = dataSource.filter(item => item.key !== key)
    },
    strToDay(str) {
      let date = new Date(str)
      return ("0" + date.getDate().toString()).slice(-2) +
              '.' + ("0" + (date.getMonth()+1).toString()).slice(-2) +
              '.' + date.getFullYear()
    },
    showModal() {
      this.$emit('kaef')
    }
  },
}
</script>